﻿@import "../../styles/_variables";

dream-header {
    font-family: 'Arial', "Helvetica Neue", Helvetica, Arial, sans-serif;

    .nav .open > a, .nav .open > a:hover, .nav .open > a:focus {
        background-color: transparent;
    }

    .navbar-nav > li > a.dropdown-toggle  {
        padding-top: 0px;
        padding-bottom: 0px;
        margin-top: 24px;
    }

    top: 0px;
    z-index: 999;
    left: 0px;
    right: 0px;
    margin: 0px auto;
    background: #ffffff;
    padding: 0;

    .nav > li > a:hover, .nav > li > a:focus {
        text-decoration: none;
        background-color: transparent;
        color: #e22004;
    }
    
    .nav > li > a:hover {
        text-decoration: underline;
    }

    .navbar-brand {
            margin: 0;
            padding: 0;
            float: left;
            font-size: 26px;
            line-height: 52px;
            cursor: pointer;

            img.logo {
                margin-right: -2px;
                top: -2px;
                position: relative;
                display: inline-block;
                width: 47px;
                opacity: 0.96;
            }

            span.pound {
                color: @link-color;
                font-weight: bold;
                font-size: 46px;
                line-height: 25px;
                position: relative;
                top: 6px;
            }

            a, a:hover {
                text-decoration: none;
            }

        }

}
